.header {
  background-color: #ffffff;
  height: 20vh;
  display: flex;
  align-items: center;
  padding: 5vh 5vw;
  justify-content: space-between;
  width: 100vw;
  z-index: 99;
  position: fixed;
}
.logo {
  font-style: none;
  text-decoration: none;
  color: black;
  font-family: boris;
  font-size: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.header-navigation {
  display: flex;
  justify-content: space-between;
  gap: 2vw;
}
.header-navigation h4 {
  padding: 10px 25px;
  border: 1px solid #0000003c;
  font-weight: 500;
  border-radius: 50px;
  color: #000000bb;
  position: relative;
  font-size: 16px;
  transition: all ease 0.4s;
  overflow: hidden;
}
.header-navigation h4::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #f9e802;
  border-radius: 50%;
  left: 0;
  bottom: -100%;
  transition: all ease 0.4s;
}
.header-navigation h4:hover::after {
  bottom: 0;
  border-radius: 0;
}
.header-navigation h4 a {
  color: #000000bb;
  text-decoration: none;
  position: relative;
  z-index: 9;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Add a smooth transition to the dropdown */
.header-dropdown-list {
  position: absolute;
  top: 64%;
  left: 50vw;
  background-color: rgba(255, 255, 255, 0.955);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  width: auto;
  height: 50vh;
  display: flex;
  flex-direction: column;
  transform: translateY(-20px); /* Initially hidden */
  opacity: 0; /* Initially invisible */
  transition: transform 0.3s ease, opacity 0.3s ease; /* Smooth transition for transform and opacity */
  border-radius: 2px;
  padding: 30px;
}
