.header-windows, .header-doors, .header-floor, .header-kitchen, .header-roof{
  height: 20%;
  border-bottom: 2px solid rgba(0, 0, 0, 0.315);
  width: 20vw;
  display: flex;
  align-items: center;
  padding: 0 20px;
  transition: all 0.3s ease;
  justify-content: space-between;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.955);
}
.header-floor-img,.header-doors-img,.header-windows-img,.header-kitchen-img,.header-roof-img{
  display: none;
  border-radius: 20px;
  height: 50vh;
  width: 40vh;
  background-color: #f9e802;
  position: fixed;
  top: 2vh;
  left: 24vw;
}

.header-windows:hover .header-windows-img{
  border-radius: 20px;
  display: block;
  background-image: url("/src/Images/windows.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

}
