.header-dropdown-list div:hover {
  padding: 0 10px;
  border-radius: 2px 2px 0 0;
  background-color: #f9e802;
  border-bottom: 2px solid rgba(0, 0, 0, 0.315);
}

.header-dropdown-button-active {
  transition: transform 0.3s ease;
  transform: rotate(180deg); /* Rotating the caret when dropdown is open */
}
.header-dropdown-button {
  transition: transform 0.3s ease;
}
.hamburger {
  display: none;
}
.menu-open {
  display: none;
  transition: all 0.5s ease-in-out;
}
.menu-open div a {
  width: 100%;
  font-size: 18;
  color:black;
  font-style: none;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}